body {
    margin: 0;
    padding: 0;
    color: #022b58;
    font-family: 'Open Sans', sans-serif;
}

* {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
}

form>div {
    padding-left: 10px;
    padding-right: 10px;
}

body hr {
    background-color: #bbbebf;
}

.page_center {
    max-width: 850px;
    width: 100%;
    margin: 0 auto;
    padding-bottom: 30px;
}

.header_text_right {
    text-align: right;
}

.page_inner {
    padding: 0 10px;
}

.app_logo {
    max-width: 50px;
}

#menu {
    padding: 0;
}

#menu .list_item {
    padding: 20px 20px 15% 20px;
}

#menu .list_item span {
    font-size: 27px;
    color: #002a59;
    font-weight: bold;
}

.center {
    text-align: center;
}

div.two_col_menu {
    padding: 10px 20px 25% 20px;
    box-shadow: none;
    border-radius: 0;
    border: 1px solid #bbbebf;
}

div.hp_tc {
    font-weight: bold;
    font-size: 20px;
    text-transform: lowercase;
    height: 100%;
}

div.hp_tc .central_local_excerpt {
    text-transform: initial;
}

.hp_two_col_top {
    border-top: 3px solid #bbbebf;
}

.two_col_menu img {
    max-width: 25px;
}

div.three_col_menu {
    padding: 10px 20px 35% 20px;
    border: 1px solid #bbbebf;
    border-radius: 0;
    box-shadow: none;
}

.three_col_menu img {
    max-width: 25px;
}

select {
    width: 100%;
}

.two_col_menu.active_location {
    background-color: #002a59;
    color: #fff;
}

div.location_type {
    padding: 20px 5px;
    text-align: center;
    font-weight: bold;
    color: #002a59;
    border-radius: 0;
    font-size: 20px;
    height: 100%;
}

.custom_field label,
label.select_label {
    padding: 0 15px;
}

.custom_field input {
    text-align: center;
    font-size: 28px;
    font-weight: bold;
}

a.link_btn,
.fileContainer button.link_btn,
button.link_btn {
    color: #fff;
    background-color: #002a59;
    padding: 6px 16px 6px 3px;
    font-size: 1.3rem;
    min-width: 64px;
    box-sizing: border-box;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    line-height: 1.75;
    font-family: 'Open Sans', sans-serif;
    font-weight: 500;
    border-radius: 4px;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
    text-decoration: none;
    border: 0;
    margin: 20px 0;
    cursor: pointer;
    display: inline-flex;
    outline: none;
    align-items: center;
    user-select: none;
    border-radius: 0;
    font-weight: bold;
}

button.link_btn:hover {
    background-color: #002a59;
}

.multiple_buttons {
    text-align: center;
}

div.q_answer_wrap {
    margin: 10px 0;
}

div.q_answer {
    padding: 10% 5%;
    text-align: center;
    border: 2px solid #012A59;
    color: #012A59;
    border-radius: none;
    font-size: 20px;
    font-weight: bold;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
}

p.final_quiz {
    color: #012A59;
    font-weight: bold;
    font-size: 20px;
}

.active_answer>div {
    background: #012A59;
    color: #fff;
}

.even_answer .q_answer {
    margin-left: 5px;
    margin-right: 10px;
}

.odd_answer .q_answer {
    margin-right: 5px;
    margin-left: 10px;
}

.question {
    color: #012A59;
    font-size: 20px;
    padding: 0 10px;
    line-height: 1.2em;
    margin: 25px 0 0 0;
    font-weight: bold;
}

.question ul li {
    margin: 10px 0;
}

div.custom_step,
div.custom_step * {
    padding: 0;
}

.custom_step svg {
    font-size: 35px;
}

.header_text {
    color: #012A59;
    font-size: 20px;
    font-weight: bold;
}

.header_text span {
    color: #ff0021;
}

.custom_step svg:not(.step_active):not(.completed_step) {
    color: #fff;
    border: 2px solid #012A59;
    border-radius: 100%;
}

.custom_step svg:not(.step_active):not(.completed_step) text {
    fill: #012A59;
    font-weight: bold;
    font-size: 13px;
}

.textarea_feedback label {
    padding: 10px 15px;
    box-sizing: border-box;
}

.textarea_feedback textarea {
    padding: 0 15px;
    box-sizing: border-box;
}

div.textarea_feedback {
    border: 1px solid #adadad;
}

a.link_btn.reset_toate_btn {
    padding: 10px 5px 10px 15px;
    margin: 15px 0;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
    background: #ff0021;
    max-width: 150px;
    width: 100%;
    text-align: center;
    font-weight: bold;
}

.save_all_button {
    max-width: 150px;
    width: 100%;
}

.logout_button {
    position: relative;
    top: 1px;
}

div.expanded_input input,
div.expanded_input textarea {
    padding-top: 20px;
    padding-bottom: 20px;
    transition: 0.15s all ease;
    font-size: 25px
}

.excerpt {
    font-size: 14px;
}

#select-tara,
#select-sex,
#select-tip_problema,
#select-selector_partide,
#select-judet,
#select-sectie {
    padding-left: 10px;
}

.add_padding {
    padding: 0 15px;
}

.stats_list .stats_list_item {
    border-top: 1px solid #adadad;
    text-align: center;
}

.stats_list .stats_list_item:last-child {
    border-bottom: 1px solid #adadad;
}

.stats_list_item span,
.stats_list_item .stats_list_item_inner {
    color: #002a59;
    font-weight: bold;
    font-size: 16px;
}

#menu .list_item_grayed_out span {
    color: #adadad;
}

a.reset_btn {
    background: #ff0021;
}

.radio_label span:last-child {
    font-weight: bold;
    font-size: 20px;
    color: #002a59;
}

div.rating_select {
    padding: 20px 0;
    margin-top: 0 !important;
}

.rating_select label {
    padding: 0 20px;
}

#select-rating {
    padding: 10px 25px 10px 15px;
}

.persoane_adaugate_title {
    text-align: center;
}

.custom_error input {
    color: #f00;
    border-color: #f00;
}

.custom_error input:after,
.custom_error input:before {
    color: #f00;
    border-color: #f00;
}

label.select_label_rating {
    font-size: 20px;
    margin-top: 30px;
}

p.ghid_question {
    font-weight: bold;
    font-size: 18px;
}

.ghid_list {
    padding: 0 0 0 15px;
    margin: 0;
}

.ghid_list li {
    margin: 10px 0;
    font-size: 17px;
}

p.ghid_text {
    font-size: 17px;
}

.ghid_text ul {
    margin: 0;
}

a.list_item_modified {
    background: #01A1F7;
}

#menu a.list_item_modified span {
    color: #fff;
}

a.section_save_button,
button.section_save_button,
.fileContainer button.link_btn,
button.link_btn {
    padding-left: 16px;
}

.global_link a {
    font-weight: bold;
    color: #012A59;
    font-size: 16px;
}

button.i_quiz_forward_button {
    color: rgba(0, 0, 0, 0.87);
    font-size: 0.875rem;
    line-height: 1.75;
    font-weight: 500;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    text-transform: uppercase;
    background: transparent;
    box-shadow: none;
}

button.i_quiz_forward_button:hover {
    background: rgba(0, 0, 0, 0.08);
}


/* Always set the map height explicitly to define the size of the div
       * element that contains the map. */

#map {
    height: 600px;
}


/* Optional: Makes the sample page fill the window. */

#description {
    font-family: Roboto;
    font-size: 15px;
    font-weight: 300;
}

#infowindow-content .title {
    font-weight: bold;
}

#infowindow-content {
    display: none;
}

#map #infowindow-content {
    display: inline;
}

.pac-card {
    margin: 10px 10px 0 0;
    border-radius: 2px 0 0 2px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    background-color: #fff;
    font-family: Roboto;
}

#pac-container {
    padding-bottom: 12px;
    margin-right: 12px;
}

.pac-controls {
    display: inline-block;
    padding: 5px 11px;
}

.pac-controls label {
    font-family: Roboto;
    font-size: 13px;
    font-weight: 300;
}

#pac-input {}

#pac-input:focus {
    border-color: #4d90fe;
}

#title {
    color: #fff;
    background-color: #4d90fe;
    font-size: 25px;
    font-weight: 500;
    padding: 6px 12px;
}

#target {
    width: 345px;
}

#map_form {
    /*display: none; */
}

a.green_status_button,
a.red_status_button {
    display: inline-block;
    padding: 5px 15px;
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    max-width: 150px;
    width: 100%;
    margin: 0;
}

a.green_status_button {
    background: #002a59;
    text-align: center;
    font-size: 13px;
    padding-left: 5px;
    padding-right: 5px;
    max-width: 200px;
    width: 100%;
    display: inline-block;
}

.info_buttons {
    text-align: center;
}

a.delete_marker_from_app_btn {
    background: #f00;
}

.checked_radio_button_red svg,
.radio_button_red svg {
    color: #f00;
}

.checked_radio_button_green svg,
.radio_button_green svg {
    color: #00BE04;
}

.map_status_button svg {
    font-size: 50px;
}

.map_server_response {
    text-align: center;
    font-weight: bold;
}

div.map_status_button .radio_label {
    display: inline-block;
    width: 100%;
    margin-right: 0;
    margin-left: 0;
}

div.map_status_button .radio_label span:last-child {
    font-size: 15px;
}

.fake_label_text {
    margin: 0;
    font-size: 15px;
    text-transform: uppercase;
}

.fileUploader .fileContainer {
    box-shadow: none;
}

.fileContainer button.upload_image_btn {
    background: transparent url('/images/image_upload_icon.png') no-repeat center / contain;
    height: 70px;
    width: 70px;
}

.fileContainer button.upload_image_btn:hover {
    background: transparent url('/images/image_upload_icon.png') no-repeat center / contain;
}

.fileContainer .errorsContainer {}

.status_afis_vizual strong {
    font-weight: bold;
}

.image_upload_server_error {
    text-align: center;
    color: #f00;
    font-size: 14px;
    display: inline-block;
    width: 100%;
}

.raport_save_success_message {
    text-align: center;
    color: rgba(54, 207, 40, 0.842);
    font-size: 16px;
    display: inline-block;
    width: 100%;
    font-weight: bold;
}

.upload_progress {
    text-align: center;
}


/**VOTARE APP**/

button.login_submit_button {
    margin-top: 20px;
    font-family: 'Open Sans', sans-serif;
